	module.exports = {"data":{"markdownRemark":{"html":"<p>Append whichever city you want to visit after <a href=\"nextbus.to/\">nextbus.to/</a> (ex: nextbus.to/toronto) to get the next departing bus and price. Booking is a couple of clicks away.</p>\n<h1 class=\"w-100 text-center my-7\">\n  Sure you usually plan your trips, but spontaneity is exciting. \n</h1>\n<h2>A hackathon project with a goal</h2>\n<p>Nextbus was a 3 days hackathon project at Busbud. We wanted to simplify to the maximum the process on jumping on the next bus by intuitively delivering the very next bus leaving from your current location. A select group of Busbud users were consistently travelling on short notice, so could we make that experience better for them?</p>\n<p>Nextbus was well received with 100+ up votes on <a href=\"https://www.producthunt.com/posts/nextbus-to\">Product Hunt</a> and thousands of visits from launch.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-a9dd8.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 788px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 66.63366336633663%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"nextbus banner\" title=\"\" src=\"/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-28af6.jpg\" srcset=\"/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-6ab74.jpg 197w,\n/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-68503.jpg 394w,\n/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-28af6.jpg 788w,\n/static/nextbus-banner-a2ff47a5ce638a037ec1b4d9ae01a9a3-a9dd8.jpg 1010w\" sizes=\"(max-width: 788px) 100vw, 788px\">\n    </span>\n  </span>\n  \n  </a>\n    ","frontmatter":{"date":"2016-05-01T00:00:00Z","path":"/nextbus","title":"Nextbus.to","timeframe":"Jun - Jun 2016","images":{"first":"nextbus-1","second":"nextbus-2"},"gradient":{"left":"#73f2b5","right":"#73f2d5"}}}},"pathContext":{}}

